@import url(https://fonts.googleapis.com/css?family=Coda);
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Coda', cursive;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#navbar {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  top: 0;
  position: fixed;
  z-index: 9001;
  width: 100%;
  background: #fff;
  padding: 10px;
  text-align: center;
  box-shadow: rgba(0, 0, 0, .3) 0 0 10px;
}

#logo {
  font-size: 12px;
  border: 2px solid black;
  padding: 3px;
  border-radius: 2px;
  margin-right: 10px;
}

#login form,
#logout form {
  display: -webkit-flex;
  display: flex;
}

#login form button,
#logout form button {
  margin-left: 10px;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

input,
button {
  outline: none;
}

button {
  border: 2px solid black;
  border-radius: 2px;
  background: transparent;
  padding: 5px;
  cursor: pointer;
}

.d-none {
  display: none;
}

#notes {
  padding: 91px 10px 10px;
  min-height: 100vh;
  text-align: center;
  position: relative;
}

.note {
  width: 280px;
  position: absolute;
  text-align: left;
  left: calc(50% - 130px);
  border-radius: 2px;
  padding: 20px;
  box-shadow: rgba(0, 0, 0, .3) 0 0 10px;
  cursor: move;
}

.notification {
  position: absolute;
  text-align: center;
  font-size: 12px;
  top: 0;
  left: 0;
  right: 0;
  padding: 10px;
}

.note .owl-field {
  margin: 10px 0;
}

.note textarea {
  font-family: inherit;
  resize: none !important;
  font-size: 24px;
  font-weight: inherit;
  width: 100%;
  outline: none;
  padding: 0;
}

.owl-field input,
.owl-field textarea {
  border: none;
  background: none;
}

.owl-field input+.border,
.owl-field textarea+.border {
  background: #000;
  height: 2px;
  display: block;
  top: 100%;
  width: 100%;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  transition: -webkit-transform .25s;
  transition: transform .25s;
  transition: transform .25s, -webkit-transform .25s;
}

.owl-field input:hover+.border,
.owl-field input:focus+.border,
.owl-field textarea:hover+.border,
.owl-field textarea:focus+.border {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}

.note:focus-within,
.react-draggable-dragging {
  z-index: 0;
  -webkit-animation: toTop forwards;
          animation: toTop forwards;
}

@-webkit-keyframes toTop {
  0% {
    z-index: 0;
  }

  100% {
    z-index: 9000;
  }
}

@keyframes toTop {
  0% {
    z-index: 0;
  }

  100% {
    z-index: 9000;
  }
}

.color-yellow {
  background: #FFFF88;
}

.color-blue {
  background: #88FFFF;
}

.color-red {
  background: #FF8888;
}

.color-green {
  background: #88FF88;
}

.note-controls {
  display: -webkit-flex;
  display: flex;
}

.color-changer {
  justify-self: flex-end;
}

.color-input {
  display: inline-block;
  margin: 5px;
  border-radius: 50%;
  border: 2px solid black;
}

.note-date {
  pointer-events: none;
  color: #666;
  font-size: 12px;
}

/* CHECKBOX */
[type=checkbox] {
  visibility: hidden;
  /* <-- hide the default checkbox, the rest is to hide and allow tabbing, which display:none prevents */
  display: block;
  height: 0;
  width: 0;
  position: absolute;
  overflow: hidden;
}

.color-input .checkmark {
  /* <-- style the artificial checkbox */
  position: relative;
  height: 25px;
  width: 25px;
  display: block;
}

[type=checkbox]+.checkmark:hover::after,
[type=checkbox]:checked+.checkmark::after {
  content: '';
  position: absolute;
  width: 14px;
  height: 9px;
  background: 0 0;
  top: 5px;
  left: 5px;
  border-top: none;
  border-right: none;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

[type=checkbox]+.checkmark:hover::after {
  border-left: 3px solid #888;
  border-bottom: 3px solid #888;
}

[type=checkbox]:checked+.checkmark:hover::after,
[type=checkbox]:checked+.checkmark::after {
  border-left: 3px solid #000;
  border-bottom: 3px solid #000;
}

@media only screen and (max-width: 600px) {
  .note {
    margin: 20px auto;
    position: unset;
    -webkit-transform: translate(0) !important;
            transform: translate(0) !important;
  }
}
